.login {
  height: 100%;

  &__container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__form {
    background-color: #fff;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    width: 40%;
    min-width: 40%;
    padding: 16px;
    margin-bottom: 200px;

    &-title {
      font-weight: 700;
      font-size: 20px;
      line-height: 23px;
      color: #646464;
      margin: 0;
    }

    &-label {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #646464;
      margin-top: 24px;

      .form-control {
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #646464;
        opacity: 0.69;
        min-height: 35px;
        margin-top: 3px;

        &:focus {
          box-shadow: none;
        }
      }

      @media screen and (max-width: 576px) {
        &-desktop {
          display: none;
        }
      }
    }

    &-forgot {
      margin: 24px 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #646464;
      display: block;

      &:hover {
        color: #646464;
      }
    }

    @media screen and (max-width: 576px) {
      &-forgot {
        display: none;
      }
    }

    &-login {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #646464;
      font-style: italic;
      margin: 24px 0 0 0;
      display: none;
    }

    @media screen and (max-width: 576px) {
      &-login {
        display: block;
      }
    }

    &-button {
      margin-left: auto;
      display: block;

      &-mobile {
        display: none;
      }

      @media screen and (max-width: 576px) {
        &-desktop {
          display: none;
        }

        &-mobile {
          margin: 24px auto 0;
          display: block;
        }
      }
    }

    //   modal

    &-modal {
      position: fixed;
      left: 0;
      top: 73px;
      width: 100%;
      height: calc(100% - 73px);
      background-color: rgba(0, 0, 0, 0.5);
      opacity: 0;
      visibility: hidden;
      transform: scale(1.1);
      transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;

      &-content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        min-height: 230px;
        width: 25%;
        background-color: #f7f7f7;
        box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
        border-radius: 4px;

        &-body {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          height: 100%;
          min-height: 230px;
          padding: 0 100px;

          &-title {
            font-weight: 700;
            font-size: 20px;
            line-height: 23px;
            color: #333333;
            text-align: center;
          }

          &-btn {
            &:hover {
              color: #fff;
            }
          }
        }
      }
    }

    .show-modal {
      opacity: 1;
      visibility: visible;
      transform: scale(1.0);
      transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
    }

  }

  @media screen and (max-width: 576px) {
    &__form {
      width: 100%;
    }
  }
}
