.forgot {
  height: 100%;

  &__container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__form {
    background-color: #fff;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    width: 40%;
    min-width: 40%;
    padding: 16px;

    &-title {
      font-weight: 700;
      font-size: 20px;
      line-height: 23px;
      color: #646464;
      margin: 0;
    }

    &-label {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #646464;
      margin-top: 24px;

      .form-control {
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #646464;
        opacity: 0.69;
        min-height: 35px;
        margin-top: 3px;

        &:focus {
          box-shadow: none;
        }
      }

    }

    &-button {
      margin-left: auto;
      display: block;
      margin-top: 24px;

      span {
        display: none;
      }

      @media screen and (max-width: 576px) {
        span {
          display: inline-block;
        }
      }
    }

    &-desc {
      display: none;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #646464;
      margin-top: 24px;
      text-align: center;
    }

    @media screen and (max-width: 576px) {
      &-desc {
        display: block;
      }
    }
  }

  @media screen and (max-width: 576px) {
    &__form {
      width: 100%
    }
  }
}
