.nurse {
  margin: 16px 0 16px 0;

  .nav {
    &-link {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #646464;
      display: flex;
      align-items: center;
      column-gap: 6px;
      padding: 8px 16px 13px 16px;
    }
  }

  .nav-tabs .nav-link.active {
    font-weight: 700;
  }

  .nav-tabs {
    border-bottom: none;
  }

  .tab-content {
    background-color: #f6f6f6;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 0 4px 4px 4px;
    padding: 24px;

    .tab-pane {

      &-top {
        margin-top: 25px;

        &-btn {
          padding: 6px 24px;
          background-color: #d9d9d9;
          border-radius: 4px;
          font-weight: 400;
          font-size: 16px;
          line-height: 19px;
          color: #646464;
        }
      }

      &-header {
        display: flex;
        align-items: center;
        justify-content: space-between;

        &-left {
          form {
            display: flex;
            align-items: center;
            column-gap: 8px;

            .form-control {
              min-width: 390px;
              padding: 5px 9px;

              &:focus {
                box-shadow: none;
              }

              &::placeholder {
                font-weight: 400;
                font-size: 16px;
                line-height: 19px;
                color: #646464;
              }
            }

            @media screen and (max-width: 768px) {
              .form-control {
                min-width: 300px;
              }
            }
            @media screen and (max-width: 576px) {
              .form-control {
                min-width: auto;
              }
            }
          }

          &-search {
            display: block;
            background-color: #53a551;
            border: 1px solid #f3f3f3;
            border-radius: 4px;
            padding: 5px 9px;

            &:hover {
              cursor: pointer;
            }
          }
        }

        @media screen and (max-width: 576px) {
          &-left {
            width: 100%;
          }
        }

        &-right {
          .list-group {
            column-gap: 16px;
            border-radius: inherit;

            .list-group-item {
              padding: 5px 9px;
              background-color: transparent;
              border: 1px dashed #333333;
              border-radius: 4px;

              &:last-child {
                background-color: #646464;
                border: 1px solid #f3f3f3;
                border-radius: 4px;
              }

              &:hover {
                cursor: pointer;
              }
            }
          }
        }

        @media screen and (max-width: 768px) {
          &-right {
            .list-group {
              flex-direction: inherit;
            }
          }
        }
      }

      @media screen and (max-width: 576px) {
        &-header {
          align-items: flex-start;
          flex-direction: column;
          row-gap: 16px;
        }
      }

      &-date {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        column-gap: 24px;
        row-gap: 10px;
        margin-top: 25px;

        &-calendar {
          flex: 1;
          display: flex;
          flex-direction: column;
          row-gap: 8px;
          font-weight: 700;
          font-size: 16px;
          line-height: 19px;
          color: #333333;
          margin-bottom: 24px;

          &-input {
            position: relative;


            img {
              position: absolute;
              top: 15%;
              left: 10px;
            }

            input {
              background-color: #fff;
              border: 1px solid #dde3e7;
              border-radius: 5px;
              outline: none;
              padding: 8px 8px 8px 35px;
              width: 100%;

              &::placeholder {
                font-weight: 400;
                font-size: 16px;
                line-height: 19px;
                color: #646464;
              }
            }
          }

        }

        @media screen and (max-width: 768px) {
          &-calendar {
            flex: 0 0 100%;
          }
        }
      }

      .table {
        margin-top: 25px;

        tr {
          &:hover {
            background-color: #e0e0e0;
            cursor: pointer;
          }
        }
      }

      &-pagination {
        display: flex;
        justify-content: flex-end;
        align-items: center;
      }

    }
  }


  .modal-body {
    text-align: center;

    &-title {
      margin: 0 0 16px 0;
      font-weight: 700;
      font-size: 20px;
      line-height: 23px;
      color: #333333;
    }

    &-desc {
      margin: 0 auto 24px;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #646464;
      width: 415px;
      min-width: 415px;
    }

    &-btn {
      margin-bottom: 24px;
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      color: #f3f3f3;
      background-color: #646464;
      border: 1px solid #dde3e7;
      border-radius: 5px;
      padding: 12px 32px;
    }

    &-file {
      border: 1px dashed #c6c6c6;
      border-radius: 5px;
      display: block;
      padding: 40px 0;

      &-title {
        font-weight: 700;
        font-size: 17px;
        line-height: 20px;
        color: #333333;
        margin: 0 0 10px 0;
      }

      &-desc {
        margin: 0 0 10px 0;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #9f9f9f;
      }

      &-info {
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        color: #1762f8;
        text-decoration: underline;
      }
    }
  }
}
