.school__nurse {
  margin-top: 20px;

  .nav {
    column-gap: 8px;
    border-bottom: none;

    &-link {
      flex: 1;
      background-color: #e1e1e1;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      row-gap: 5px;
      padding: 18px 0;
      border-radius: 8px;
      border-bottom: none;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      letter-spacing: -0.24px;
      color: #94969c;

      svg {
        fill: #94969c;
      }
    }

    .active {
      color: #fff;
      background-color: #52a551;
      box-shadow: 0 2px 2px rgba(128, 178, 77, 0.37);

      svg {
        fill: #fff;
      }
    }
  }

  @media screen and (max-width: 768px) {
    .tabs-desktop {
      position: fixed;
      bottom: 0;
      left: 0;
      background: linear-gradient(0deg, #FDFDFD, #FDFDFD), #FFFBFE;
      width: 100%;
      z-index: 9999;

      .nav-link {
        background-color: transparent;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        letter-spacing: -0.24px;
        color: #94969c;
        border: none;

        svg {
          fill: #94969c;
        }

      }

      .active {
        color: #52a551;
        box-shadow: none;

        svg {
          fill: #52a551;
        }
      }
    }
  }

  .tab-content {
    margin-top: 20px;
    padding: 16px;
    background-color: #f6f6f6;
    min-height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;

    .tab-pane {


      &-title {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        color: #000000;
        margin: 0 0 12px 0;
      }

      &-desc {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        text-align: center;
        color: #333333;
        width: 290px;
        min-width: 290px;
        margin: 12px auto 0 auto;
      }

      &-content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 12px;

        &-desc {
          margin: 0;
          font-weight: 400;
          font-size: 16px;
          line-height: 19px;
          color: #000000;
        }
      }

      &-found {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        row-gap: 20px;
        margin: 0 auto;
        width: 100%;
        max-width: 400px;

        .btn__green {
          width: 100%;
          text-align: center;

          &:hover {
            color: #fff;
          }
        }
      }

      .btn-reset {
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        color: #f3f3f3;
        background-color: #52a551;
        border-radius: 4px;
        padding: 8px 24px;
        width: 100%;
        margin-top: 8px;
      }

      &-calendar {
        background-color: #fff;
        padding: 24px 16px;
        margin-top: 24px;

        &-info {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 4px;

          &-date {
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #646464;
          }

          &-tooltip {
            position: relative;
            display: inline-block;
            cursor: pointer;

            &-text {
              width: 90px;
              height: 50px;
              background-color: #141414;
              border-radius: 4px;
              padding: 12px 7px;
              font-weight: 400;
              font-size: 10px;
              line-height: 12px;
              color: #fff;
            }
          }
        }

        &-input {
          position: relative;

          &-img {
            position: absolute;
            top: 13%;
            left: 7px;
          }

          input {
            background-color: #fff;
            border: 1px solid #dde3e7;
            border-radius: 5px;
            padding-left: 30px;
            width: 100%;

            &::placeholder {
              font-weight: 400;
              font-size: 16px;
              line-height: 19px;
              color: #646464;
            }
          }
        }

        label {
          display: flex;
          flex-direction: column;
          row-gap: 4px;
        }
      }

    }
  }

  &-nav {
    .nav-link {
      padding: 10px 6px;
    }
  }

  .history-content {
    display: inherit;
    padding: 0;

    &-wrapper {
      display: flex;
      flex-wrap: wrap;
      column-gap: 8px;
      row-gap: 8px;
    }

    &-title {
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      color: #000000;
      margin: 0 0 8px 0;
    }

    &-card {
      padding: 8px 8px 23px 8px;
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
      border-radius: 4px;
      width: calc(33.45% - 8px);

      &-title {
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        margin: 0 0 4px 0;
        color: #000000;
      }

      &-desc {
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        color: #000000;
      }

    }

    @media screen and (max-width: 768px) {
      &-card {
        width: 100%;
      }
    }

    &-footer {
      width: 100%;
      padding: 8px;
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
      border-radius: 4px;
      background-color: #fff;

      &-info {
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        color: #000000;
        margin: 0;
      }
    }

    &-item {
      background-color: #fbfbfb;
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
      border-radius: 4px;
      padding: 16px;
      margin-bottom: 16px;

      &-top {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &-title {
          flex: 1;
          font-weight: 500;
          font-size: 16px;
          line-height: 19px;
          color: #333333;
          margin: 0 0 8px 0;
        }

      }

      &-bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &-title {
          flex: 1;
          font-weight: 400;
          font-size: 16px;
          line-height: 19px;
          margin: 0;
        }
      }
    }

  }

  .modal-content {
    .modal-body {
      &-title {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        color: #333333;
        text-align: center;
        margin: 0 0 24px 0;
      }

      &-desc {
        margin: 0 0 24px 0;
        font-weight: 500;
        font-size: 18px;
        line-height: 23px;
        text-align: left;
        color: #333333;
      }


      &-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        column-gap: 16px;

        &-outline {
          font-weight: 700;
          font-size: 16px;
          line-height: 19px;
          color: #646464;
          background-color: #f3f3f3;
          border: 1px solid #0b1f5d;
          border-radius: 4px;
          padding: 8px 24px;
        }

        &-green {
          background-color: #52a551;
          border-radius: 4px;
          padding: 8px 24px;
          font-weight: 700;
          font-size: 16px;
          line-height: 19px;
          color: #f3f3f3;
        }
      }
    }
  }
}

@media screen and (max-width: 768px){
  .school__nurse{
    padding-bottom: 150px;
  }
}
