.reset {
  height: 100%;

  &__container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__form {
    background-color: #fff;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    width: 40%;
    min-width: 40%;
    padding: 16px;

    &-title {
      font-weight: 700;
      font-size: 20px;
      line-height: 23px;
      color: #646464;
      margin: 0;
    }

    &-label {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #646464;
      margin-top: 24px;
      position: relative;

      .form-control {
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #646464;
        opacity: 0.69;
        min-height: 35px;
        margin-top: 3px;


        &:focus {
          box-shadow: none;
        }
      }

      .password-icon {
        position: absolute;
        right: 3%;
        top: 50%;

        &:hover {
          cursor: pointer;
        }
      }

    }

    &-button {
      margin-left: auto;
      display: block;
      margin-top: 16px;
    }

  }

  @media screen and (max-width: 576px) {
    &__form {
      width: 100%;
    }
  }
}
