
html {
  box-sizing: border-box;
  font-family: Roboto, sans-serif !important
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: Roboto, sans-serif !important;
  height: 100%;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  background-color: #f0f0f0;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  height: 100%;
  //overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку

}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }


}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}


.btn__green {
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #f3f3f3;
  background-color: #52a551;
  border-radius: 4px;
  padding: 8px 24px;
}

.btn__outline {
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  background-color: #f3f3f3;
  border: 1px solid #0b1f5d;
  border-radius: 4px;
  color: #646464;
  padding: 8px 24px;
}


.form-select {
  background-image: url("../img/select-bg.svg");
  background-size: 12px 12px;
}

.form-control {
  &:focus {
    box-shadow: none;
  }
}

.text-red {
  color: #ff4040 !important;
}

.text-green {
  color: #53a551 !important;
}

.tab-pane-calendar-info-tooltip-text {
  visibility: hidden;
  position: absolute;
  bottom: 130%;
  right: -10px;
}

.tab-pane-calendar-info-tooltip:hover .tab-pane-calendar-info-tooltip-text {
  visibility: visible;
}


.tab-pane-calendar-info-tooltip-text:after, .tab-pane-calendar-info-tooltip-text:before {
  top: 100%;
  left: 77%;
  border: solid transparent;
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.tab-pane-calendar-info-tooltip-text:after {
  border-color: rgba(136, 183, 213, 0);
  border-top-color: #000;
  border-width: 10px;
  margin-left: -10px;
}

.tab-pane-calendar-info-tooltip-text:before {
  border-color: rgba(194, 225, 245, 0);
  border-top-color: #000;
  border-width: 10px;
  margin-left: -10px;
}

.tabPane {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}

.disabled__btn {
  opacity: 0.5;
  pointer-events: auto;
  cursor: not-allowed !important;
}
