.float {
  margin-top: 20px;

  .nav {
    column-gap: 8px;
    border-bottom: none;

    &-link {
      flex: 1;
      background-color: #e1e1e1;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      row-gap: 5px;
      padding: 18px 0;
      border-radius: 8px;
      border-bottom: none;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      letter-spacing: -0.24px;
      color: #94969c;

      svg {
        fill: #94969c;
      }
    }

    .active {
      color: #fff;
      background-color: #52a551;
      box-shadow: 0 2px 2px rgba(128, 178, 77, 0.37);

      svg {
        fill: #fff;
      }
    }
  }

  @media screen and (max-width: 768px) {
    .tabs-desktop {
      position: fixed;
      bottom: 0;
      left: 0;
      background: linear-gradient(0deg, #FDFDFD, #FDFDFD), #FFFBFE;
      width: 100%;
      z-index: 9999;

      .nav-link {
        background-color: transparent;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        letter-spacing: -0.24px;
        color: #94969c;
        border: none;

        svg {
          fill: #94969c;
        }

      }

      .active {
        color: #52a551;
        box-shadow: none;

        svg {
          fill: #52a551;
        }
      }
    }
  }

  .tab-content {
    margin-top: 20px;
    padding: 16px;
    background-color: #f6f6f6;
    min-height: 400px;
    display: flex;
    justify-content: center;

    .tab-pane {

      &-title {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        color: #000000;
        margin: 0;
      }

      &-content {
        background-color: #fbfbfb;
        box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
        border-radius: 4px;
        margin-top: 16px;
        padding: 16px;

        &-card {
          display: flex;
          justify-content: space-between;
          align-items: center;
          flex-wrap: wrap;

          &-info {
            flex: 0 0 1;

            span {
              font-weight: 400;
              font-size: 16px;
              line-height: 19px;
              color: #9f9f9f;
              display: block;
              width: 230px;
              min-width: 230px;
            }

            &-title {
              margin: 0 0 8px 0;
              font-weight: 500;
              font-size: 16px;
              line-height: 19px;
              color: #000000;
            }

            &-desc {
              font-weight: 400;
              font-size: 16px;
              line-height: 19px;
              color: #000000;
              margin: 0;
            }

          }

        }

        @media screen and (max-width: 576px) {
          &-card {
            row-gap: 10px;
          }
        }


        .btn-reset {
          background-color: #f3f3f3;
          border: 1px solid #0b1f5d;
          border-radius: 4px;
          font-weight: 700;
          font-size: 16px;
          line-height: 19px;
          color: #646464;
          padding: 8px 24px;
          margin-top: 24px;
        }

        .btn-green {
          background-color: #52a551;
          border-radius: 4px;
          border: 1px solid #52a551;
          color: #fff;
        }

      }

      .nav-link {
        padding: 10px 20px;
      }

      &-picked {
        padding: 16px;
        background-color: #fff;
        margin-top: 16px;

        &-item {
          display: flex;
          flex-wrap: wrap;
          margin-bottom: 24px;

          &-card {
            flex: 1 0 0;
          }

          &-title {
            font-weight: 700;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
            margin: 0 0 8px 0;
          }

          &-desc {
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
            margin: 0;
          }
        }

        &-calendar {
          display: flex;
          flex-direction: column;
          row-gap: 8px;
          font-weight: 700;
          font-size: 16px;
          line-height: 19px;
          color: #333333;
          margin-bottom: 24px;

          &-input {
            position: relative;


            img {
              position: absolute;
              top: 15%;
              left: 10px;
            }

            input {
              background-color: #fff;
              border: 1px solid #dde3e7;
              border-radius: 5px;
              outline: none;
              padding: 8px 8px 8px 35px;
              width: 100%;

              &::placeholder {
                font-weight: 400;
                font-size: 16px;
                line-height: 19px;
                color: #646464;
              }
            }
          }

        }
      }
    }
  }

  .modal-content {
    .modal-body {
      &-title {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        color: #333333;
        text-align: center;
        margin: 0 0 24px 0;
      }

      &-desc {
        margin: 0 0 24px 0;
        font-weight: 500;
        font-size: 18px;
        line-height: 23px;
        text-align: center;
        color: #333333;
      }

      &-center {
        margin: 0 0 24px 0;
        font-weight: 500;
        font-size: 20px;
        line-height: 23px;
        text-align: center;
        color: #333333;
      }

      &-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        column-gap: 16px;

        &-outline {
          font-weight: 700;
          font-size: 16px;
          line-height: 19px;
          color: #646464;
          background-color: #f3f3f3;
          border: 1px solid #0b1f5d;
          border-radius: 4px;
          padding: 8px 24px;
        }

        &-green {
          background-color: #52a551;
          border-radius: 4px;
          padding: 8px 24px;
          font-weight: 700;
          font-size: 16px;
          line-height: 19px;
          color: #f3f3f3;
        }
      }
    }
  }
}
