.header {
  background-color: #fff;
  padding: 8px 24px;

  &__logo {
    display: flex;
    align-items: center;
    column-gap: 8px;
    max-width: 200px;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #646464;

    &:hover {
      color: #646464;
    }
  }

  &__right {
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    .dropdown {

      .dropdown-menu {
        border: none;
      }

      .dropdown-toggle {
        background-color: #eaeaea;
        border-radius: 4px;
        padding: 8px 16px;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #646464;
        border: none;
      }
    }

    &-list {
      padding: 16px;
      background-color: #fff;

      &-link{
        display: flex;
        align-items: center;
        column-gap: 5px;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #646464;
      }

      &:not(:last-child) {
        border-bottom: 1px solid #ddd;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    background-color: #fff;
    padding: 8px 0;
  }
}
