.add {
  margin: 16px 0 16px 0;


  &__inputs {
    background-color: #f6f6f6;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 0 4px 4px 4px;
    padding: 16px;

    &-title {
      margin: 0;
      font-weight: 700;
      font-size: 20px;
      line-height: 23px;
      color: #333333;
      padding-bottom: 16px;
      border-bottom: 1px solid #e2e2e2;
    }

    &-label {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(700px, 1fr));
      grid-template-rows: auto;
      grid-column-gap: 10px;
      grid-row-gap: 0;
      margin-top: 16px;

      &-title {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        color: #333333;
        margin: 0 0 16px 0;
      }

      &-item {

        &-input {
          width: 100%;
          font-weight: 500;
          font-size: 16px;
          line-height: 19px;
          color: #333333;
          margin-bottom: 16px;

          .form-check {
            margin: 0;
            padding-left: 0;

            &-input {
              margin: 0 8px 0 0;
            }
          }

          &-number{
            margin: 8px 0 16px 0;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #646464;
            opacity: 0.69;
          }

          select {
            margin-top: 8px;
            padding: 8px;
            border: 1px solid #dde3e7;
            border-radius: 5px;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #646464;
            opacity: 0.69;

            &::placeholder {
              font-weight: 400;
              font-size: 16px;
              line-height: 19px;
              color: #646464;
              opacity: 0.69;
            }

            &:focus {
              box-shadow: none;
            }
          }

          input {
            margin-top: 8px;
            padding: 8px;
            border: 1px solid #dde3e7;
            border-radius: 5px;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #646464;
            opacity: 0.69;

            &::placeholder {
              font-weight: 400;
              font-size: 16px;
              line-height: 19px;
              color: #646464;
              opacity: 0.69;
            }

            &:focus {
              box-shadow: none;
            }
          }

        }

        &-select {
          display: flex;
          column-gap: 16px;
        }

        @media screen and (max-width: 576px) {
          &-select {
            flex-direction: column;
          }
        }

        &:nth-child(4) {
          padding-top: 55px;
        }
      }
    }

    @media screen and (max-width: 1440px) {
      &-label {
        grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
      }
    }

    @media screen and (max-width: 768px) {
      &-label {
        grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));
      }
    }

    &-textarea {
      margin-bottom: 16px;

      &-title {
        margin: 0 0 8px 0;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #333333;
      }

      textarea {
        &:focus {
          box-shadow: none;
        }
      }
    }

    &-footer {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .btn__outline {
        background-color: #f3f3f3;
        border: 1px solid #0b1f5d;
        border-radius: 4px;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        color: #646464;
        padding: 8px 24px;
      }
    }

  }
}
