.views {
  margin: 16px 0 16px 0;


  &__inputs {
    background-color: #f6f6f6;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 0 4px 4px 4px;
    padding: 16px;

    &-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #e2e2e2;
      padding-bottom: 16px;

      &-title {
        margin: 0;
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        color: #333333;
      }

      .list-group {
        column-gap: 16px;
        border-radius: inherit;

        .list-group-item {
          padding: 5px 9px;
          background-color: transparent;
          border: 1px dashed #333333;
          border-radius: 4px;

          &:hover {
            cursor: pointer;
          }
        }
      }
    }


    &-label {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(700px, 1fr));
      grid-template-rows: auto;
      grid-column-gap: 10px;
      grid-row-gap: 0;
      margin-top: 16px;

      &-title {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        color: #333333;
        margin: 0 0 16px 0;
      }

      &-item {

        &-input {
          width: 100%;
          font-weight: 500;
          font-size: 16px;
          line-height: 19px;
          color: #333333;
          margin-bottom: 16px;
          position: relative;

          .form-check {
            margin: 0;
            padding-left: 0;

            &-input {
              margin: 0 8px 0 0;
            }
          }

          select {
            margin-top: 8px;
            padding: 8px;
            border: 1px solid #dde3e7;
            border-radius: 5px;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #646464;
            opacity: 0.69;

            &::placeholder {
              font-weight: 400;
              font-size: 16px;
              line-height: 19px;
              color: #646464;
              opacity: 0.69;
            }

            &:focus {
              box-shadow: none;
            }
          }

          input {
            margin-top: 8px;
            padding: 8px;
            border: 1px solid #dde3e7;
            border-radius: 5px;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #646464;
            opacity: 0.69;

            &::placeholder {
              font-weight: 400;
              font-size: 16px;
              line-height: 19px;
              color: #646464;
              opacity: 0.69;
            }

            &:focus {
              box-shadow: none;
            }
          }

          .password-icon {
            position: absolute;
            right: 3%;
            top: 50%;

            &:hover {
              cursor: pointer;
            }
          }

          &-number{
            margin: 8px 0 16px 0;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #646464;
            opacity: 0.69;
          }

        }

        &-select {
          display: flex;
          column-gap: 16px;
        }

        &:nth-child(4) {
          padding-top: 55px;
        }
      }
    }

    @media screen and (max-width: 1440px) {
      &-label {
        grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
      }
    }

    @media screen and (max-width: 768px) {
      &-label {
        grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));
      }
    }

    &-textarea {
      margin-bottom: 16px;

      &-title {
        margin: 0 0 8px 0;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #333333;
      }

      textarea {
        &:focus {
          box-shadow: none;
        }
      }
    }

    &-footer {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .btn__outline {
        background-color: #f3f3f3;
        border: 1px solid #0b1f5d;
        border-radius: 4px;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        color: #646464;
        padding: 8px 24px;
      }

      .btn__dark {
        background-color: #646464;
        border-radius: 4px;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        color: #fff;
        padding: 8px 24px;
      }
    }

  }

  .modal {
    .modal-body {
      text-align: center;

      &-title {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        color: #333333;
        margin: 0 0 16px 0;
      }

      &-desc {
        font-weight: 400;
        font-size: 15px;
        line-height: 18px;
        color: #333333;
        margin: 0 0 24px 0;
      }

      &-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 10px;

        button {
          font-weight: 700;
          font-size: 16px;
          line-height: 19px;
          padding: 8px 22px;
          border-radius: 4px;
        }

        &-outline {
          color: #646464;
          background-color: #f3f3f3;
          border: 1px solid #0b1f5d;
        }

        &-red {
          background-color: #a80808;
          color: #fff;
          border: 1px solid #a80808;
        }
      }
    }
  }
}
