.creat__call {
  margin: 16px 0 16px 0;

  &-content {
    padding: 16px;
    background-color: #f6f6f6;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 0 4px 4px 4px;


    &-date {
      display: flex;
      justify-content: center;
      flex-direction: column;
      width: 555px;
      max-width: 100%;
      margin: 0 auto;

      &-info {
        display: flex;
        flex-wrap: wrap;
        column-gap: 16px;
        row-gap: 16px;

        &-text {
          font-weight: 400;
          font-size: 16px;
          line-height: 19px;
          color: #333333;
          display: inline-block;
        }
      }

      &-title {
        margin: 0 0 16px 0;
        font-weight: 400;
        font-size: 20px;
        line-height: 23px;
        color: #333333;
      }

      &-label {
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #333333;
        display: flex;
        flex-direction: column;
        row-gap: 8px;
        margin-bottom: 12px;
        width: 555px;
        max-width: 100%;
      }

      &-calendar {
        position: relative;

        img {
          position: absolute;
          top: 15%;
          left: 10px;
        }

        input {
          background-color: #fff;
          border: 1px solid #dde3e7;
          border-radius: 5px;
          outline: none;
          padding: 8px 8px 8px 35px;
          width: 100%;

          &::placeholder {
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #646464;
          }
        }

      }

      &-item {
        display: flex;
        flex-wrap: wrap;
        column-gap: 16px;
      }

      &-btn {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        padding: 0;
        margin: 0 auto;
      }
    }


    &-button {
      display: flex;
      justify-content: space-between;
      margin-top: 50px;
    }
  }
}
